@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,700&family=Open+Sans:wght@600&family=Poppins:wght@300&family=Schibsted+Grotesk&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  width: 100%;
  background-color: #ffffff;
  /* background-color: #f0f2f5; */
  font-family: "Poppins", sans-serif;
  color: rgb(0, 0, 0);
}
#container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
  border: 1px solid black;
}
article {
  width: 90%;
  /* border: 1px solid black; */
}
article > table {
  width: 100%;
  padding: 50px 100px;
  /* border: 1px solid cyan; */
}
article > table > thead > tr > th {
  width: 100%;
  border: 1px solid rgb(37, 36, 36);
  padding: 10px;
  font-size: 1.4em;
  color:rgb(0, 0, 0);
  /* border: 1px solid black; */
}
tbody {
  width: 100%;
  /* border: 1px solid cyan; */
}
tbody > tr > td {
  /* width: 100%; */
  border: 1px solid rgb(255, 9, 9);
  padding: 10px;
  text-align: center;
  font-size: 1.4em;
}
/* .logo{
    height:100px;
    width:400px;
    border: 1px solid white;
} */
tbody > tr > td > img {
  width: 280px;
  border-radius: 200px;
}
tbody > tr > td > img:hover {
  cursor: pointer;
}
#btn {
  width: 200px;
  padding: 10px 0;
  font-size: 14.5 px;
  border-radius: 5px;
  background-color: rgb(4, 232, 4);
  color: black;
  border: none ;
  outline: none; 
  text-align: center;
}
#btn:hover {
  cursor: pointer;
  background-color: rgb(0, 191, 255);
  color: white;
}
